<template>
  <div class="cart-page">
    <h1>Shopping Cart</h1>
    <div v-if="cartItems.length === 0" class="empty-cart-message">
      <p>Your cart is empty.</p>
    </div>
    <div v-else>
      <div class="cart-container">
        <div class="order-details">
          <h2>Order Details</h2>
          <h2>Contents:</h2>
          <ul>
            <li v-for="groupedItem in groupedCartItems" :key="groupedItem.product.id">
              {{ groupedItem.product.naziv }} ({{ groupedItem.quantity }}x)
            </li>
          </ul>
          <form @submit.prevent="makeOrder">
            <label for="name">Name:</label>
            <input type="text" id="name" v-model="orderDetails.name" required>

            <label for="phone">Phone Number:</label>
            <input type="tel" id="phone" v-model="orderDetails.phone" required>

            <label for="address">Address:</label>
            <textarea id="address" v-model="orderDetails.address" required></textarea>
            <div class="cart-summary">
              <div class="total-cost">Total Cost: {{ totalCost }} EUR</div>
            </div>
            <button type="submit" :disabled="cartItems.length === 0" class="order-button">Make Order</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Cart',
  data() {
    return {
      orderDetails: {
        name: '',
        phone: '',
        address: '',
      },
    };
  },
  computed: {
    cartItems() {
      return this.$store.getters.cartItems;
    },
    groupedCartItems() {
      const groupedItems = [];
      const cartMap = new Map();

      // Group cart items by product ID
      this.cartItems.forEach((item) => {
        const productId = item.id;
        if (cartMap.has(productId)) {
          cartMap.get(productId).quantity++;
        } else {
          cartMap.set(productId, { product: item, quantity: 1 });
        }
      });

      // Convert the map to an array
      cartMap.forEach((value) => {
        groupedItems.push(value);
      });

      return groupedItems;
    },
    totalCost() {
      // Calculate the total cost
      return this.cartItems.reduce((total, item) => total + item.cena, 0);
    },
  },
  methods: {
    transformDataForOrder() {
      const zakaznoVreme = new Date();
      zakaznoVreme.setMonth(zakaznoVreme.getMonth() + 3);
      // Transform data into the desired format
      return {
        narudzbina: {
          status: 'Pending', // You can set the default status or modify as needed
          vreme_narucivanja: new Date().toISOString(), // Set the current date and time
          zakazno_vreme: zakaznoVreme.toISOString(), // You might want to set a default or leave it empty
          adresa: this.orderDetails.address,
          telefon: this.orderDetails.phone,
          ime_prezime: this.orderDetails.name,
        },
        stavke: this.groupedCartItems.map(item => ({
          plovilo_id: item.product.id,
          komada: item.quantity,
          //moguce mjesto gresaka
        })),
      };
    },
    async makeOrder() {
      try {
        // Prepare the order data
        const orderData = this.transformDataForOrder();

        // Call the sendOrder action from the store
        await this.$store.dispatch('sendOrder', orderData);

        // Optionally, you can reset the cart or perform other actions
        // this.$store.commit('RESET_CART');
        console.log('Order created successfully');
      } catch (error) {
        console.error(error);
        // Handle error - show a notification, etc.
      }
    },
  },
};
</script>

<style scoped>
/* Your existing styles remain unchanged */

.cart-container {
  display: flex;
  flex-direction: column;
  align-items: center;  /* Center items horizontally */
  justify-content: center;  /* Center items vertically */
  margin: auto;  /* Center the container horizontally */
  margin-top: 20px;  /* Add a top margin */
  width: 80%;
  max-width: 600px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.order-details {
  width: 100%;  /* Ensure the content within order-details takes full width */
}



label {
  display: block;
  margin-bottom: 5px;
}

input,
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 5px;
}

form {
  display: flex;
  flex-direction: column;
}
</style>
