<template>
  <div class="product-card">
    <input type="hidden" :value="product.id" />
    <div class="product-info">
      <h2>{{ product.naziv }}</h2>
      <p class="description">{{ product.opis }}</p>
      <p class="category">Kategorija: {{ product.kategorija.naziv }}</p>
      <p class="price">Cena: {{ product.cena }} EUR</p>
    </div>
    <router-link :to="{ name: 'product-details', params: { id: product.id } }">
      <button @click="setCurrentProduct(product.id)" class="details-btn"> Details </button>
    </router-link>
    <button @click="addToCart" class="add-to-cart-btn" :disabled="isProductInCart"> Add to Cart </button>
  </div>
</template>

<script>
export default {
  name: 'PloviloKartica',
  props: {
    product: Object,
  },
  methods: {
    setCurrentProduct(productId) {
      this.$store.dispatch('setCurrentProductId', productId);
    },
    addToCart() {
      const productId = this.product.id;
      this.$store.dispatch('addToCart', productId);
      this.notifyUser('Product added to cart!');
    },
    notifyUser(message) {
      alert(message);
    },
  },
};
</script>

