  background-color: #3498db;
  color: #fff;
  padding: 10px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.add-to-cart-btn:hover, .details-btn:hover {
  background-color: #2980b9;
}

.add-to-cart-btn:active, .details-btn:active {
  background-color: #216a94;
}

