<template>
  <div class="products-view">
    <ProductList :products="displayedProducts" />
    <div class="pagination">
      <button @click="prevPage" :disabled="current === 0">Previous</button>
      <span>{{ current + 1 }} / {{ totalPages }}</span>
      <button @click="nextPage" :disabled="current === totalPages - 1">Next</button>
    </div>
  </div>
</template>

<script>
import ProductList from '../components/PloviloLista.vue';

export default {
  name: 'ProductsView',
  components: {
    ProductList
  },
  data() {
    return {
      current: 0,
      productsPerPage: 3
    };
  },
  computed: {
    products() {
      console.log('Products:', this.$store.state.products);
      return this.$store.state.products;
    },
    totalPages() {
      return Math.ceil(this.products.length / this.productsPerPage);
    },
    displayedProducts() {
      const start = this.current * this.productsPerPage;
      const end = start + this.productsPerPage;
      console.log('Displayed Products:', this.products.slice(start, end));
      return this.products.slice(start, end);
    }
  },
  // In your ProductsView component
  created() {
    this.$store.dispatch('fetchProducts'); // Ensure this is called when the component is created
  },

  methods: {
    nextPage() {
      if (this.current < this.totalPages - 1) {
        this.current++;
      }
    },
    prevPage() {
      if (this.current > 0) {
        this.current--;
      }
    }
  }
};
</script>


