.products-view {
  /* Add the following styles to center the buttons horizontally */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pagination {
  /* Add the following styles to center the buttons horizontally */
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.pagination button {
  margin: 0 5px;
}
</style>